import axios from 'axios'
import { Toast } from 'vant'
import store from '@/store'
import QS from 'qs'

axios.defaults.baseURL = store.state.baseUrl

// 
axios.defaults.timeout = 10000

axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'

const requestLog = false

// Add a request interceptor
axios.interceptors.request.use(function (config) {
  // Do something before request is sent
  // console.log('config', config)
  const showDialog = config.params && config.params.showDialog !== undefined ? config.params.showDialog : true
  if (showDialog) {
    Toast.loading({
      message: '',
      duration: 0
    })
  }
  return config
}, function (error) {
  // Do something with request error
  return Promise.reject(error)
})

// Add a response interceptor
axios.interceptors.response.use(function (response) {
  // Any status code that lie within the range of 2xx cause this function to trigger
  // Do something with response data
  // console.log('axios-response-success', response)
  Toast.clear()
  const data = response.data
  if (data.code === '0000') {
    // 
    return response
  } else if (data.code === '0001') {
    return Promise.reject(response)
  } else if (data.code === '0003') {
    store.commit('setDeviceId', '')
    store.commit('updateCloseShop', '')
    return Promise.reject(response)
  } else {
    return Promise.reject(response)
  }
}, function (error) {
  // Any status codes that falls outside the range of 2xx cause this function to trigger
  // Do something with response error
  // console.log('axios-response-error', error)
  if (error.response) {
    if (requestLog) console.log('error.response.data', error.response.data)
  } else if (error.request) {
    Toast('')
  }
  return Promise.reject(error)
})

/**
 * getget
 * @param {String} url [url]
 * @param {Object} params []
 */
export function get (url, params) {
  const data = {
    app: 'vue',
    deviceId: store.state.deviceId,
    ...params
  }
  if (requestLog) console.log(url, data)
  return new Promise((resolve, reject) => {
    axios.get(url, {
      params: data
    })
      .then(res => {
        if (requestLog) console.log('get--success', res.data)
        resolve(res.data)
      })
      .catch(err => {
        if (err && err.data) {
          if (requestLog) console.log('get--fail', err.data)
          reject(err.data)
        }
      })
  })
}
/**
* postpost
* @param {String} url [url]
* @param {Object} params []
*/
export function post (url, params) {
  const data = {
    app: 'vue',
    deviceId: store.state.deviceId,
    ...params
  }
  if (requestLog) console.log(url, data)
  return new Promise((resolve, reject) => {
    axios.post(url, QS.stringify(data))
      .then(res => {
        if (requestLog) console.log('post--success', res.data)
        resolve(res.data)
      })
      .catch(err => {
        if (requestLog) console.log('post--fail', err.data)
        reject(err.data)
      })
  })
}

export function uploadFile (url, fileKey, file, data) {
  const formData = new FormData()
  formData.append(fileKey, file)
  formData.append('app', 'vue')
  formData.append('deviceId', store.state.deviceId)
  for (const key in data) {
    if (Object.hasOwnProperty.call(data, key)) {
      formData.append(key, data[key])
    }
  }
  return new Promise((resolve, reject) => {
    axios.post(url, formData, {
      'Content-type': 'multipart/form-data'
    }).then(res => {
      // 
      if (requestLog) console.log('post--file--success', res.data)
      resolve(res.data)
    }, err => {
      // 
      if (requestLog) console.log('post--file--fail', err.data)
      reject(err.data)
    })
  })
}
